	module.exports = {"data":{"markdownRemark":{"html":"<h1>推文分支</h1>\n<blockquote>\n<p>目前在开发阶段未单独拉取推文分支，未来会拉取。提交文章到 <em>jazzysnail@outlook.com</em>。</p>\n</blockquote>\n<h1>Markdown 文档信息</h1>\n<p>文档信息语法如下：</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">---\npath: \"/format\"\ndate: \"2018-03-19\"\ntitle: \"推文说明\"\nauthor: \"Leon\"\ndsct: \"这将使我们的文档更美观更易于阅读\"\ntags: [\"官方\"]\n---</code></pre>\n      </div>\n<p><strong>PS:</strong> <code>dsct</code> 和 <code>tags</code> 字段可以不声明，其中 <code>dsct</code> 用于捕捉文章的描述，如果未声明则自动摘抄 50 字。<code>author</code> 字段在系统运行编译时将自动创建作者对应的 <code>tag</code> 到 <code>tags</code> 所以作者将无需再次声明作者标签，作者标签将以特殊的颜色显示（默认为蓝色）。</p>\n<h1>优化阅读</h1>\n<p>这一部分将给予一定优化阅读的风格指南，来保证我们文章的可读性。使读者可以将关注点放在文章的思想表达上不被糟糕的排版所影响，轻松的完成高质量的阅读。为此我们将不懈努力。</p>\n<h2>代码块</h2>\n<ul>\n<li>使用 markdown 代码块时必须指认代码所用语言，用于自动化语言识别。</li>\n<li>代码块上下必须保留空行，保证与其他语法保持隔离。</li>\n</ul>\n<h2>留白（盘古之白）</h2>\n<h3>中英语言切换之间</h3>\n<p><strong>Good:</strong></p>\n<blockquote>\n<p>我是 Leon 的表弟，PHP 是世界上最好的语言。</p>\n</blockquote>\n<p><strong>Bad:</strong></p>\n<blockquote>\n<p>我是Leon本人，Java才是。</p>\n</blockquote>\n<hr>\n<h3>数字与单位之间</h3>\n<p><strong>Good:</strong></p>\n<blockquote>\n<p>这个方法的执行耗时 20 ms。</p>\n</blockquote>\n<p><strong>Bad:</strong></p>\n<blockquote>\n<p>这个方法执行时创建了10mb的执行空间。</p>\n</blockquote>\n<hr>\n<h3>中文链接之间</h3>\n<p>当然这里在语法渲染时已经经过了处理，但是依然建议在进行原文书写是保留空格提升个人书写时的体验。</p>\n<p><strong>Good:</strong></p>\n<blockquote>\n<p>这里是我的 <a href=\"http://jazzysnail.me\">个人博客</a> 欢迎往来。</p>\n</blockquote>\n<hr>\n<h3>在这些地方你不应该留白</h3>\n<ul>\n<li>以全角形式标点之前和之后没有额外的空格。</li>\n</ul>\n<h2>标点</h2>\n<h3>避免重复标点符号</h3>\n<p><strong>Good:</strong></p>\n<blockquote>\n<p>太棒啦！</p>\n</blockquote>\n<p><strong>Bad:</strong></p>\n<blockquote>\n<p>反杀？？？</p>\n</blockquote>\n<hr>\n<h3>中文文章使用全角标点</h3>\n<p><strong>Good:</strong></p>\n<blockquote>\n<p>我可爱么？喵~</p>\n</blockquote>\n<p><strong>Bad:</strong></p>\n<blockquote>\n<p>死基佬,滚!</p>\n</blockquote>\n<hr>\n<h3>英文整句使用半角标点</h3>\n<p><strong>Good:</strong></p>\n<blockquote>\n<p>The \"black mirror\" of the title is the one you'll find on every wall, on\nevery desk, in the palm of every hand: the cold, shiny screen of a TV, a\nmonitor, a smartphone.</p>\n</blockquote>\n<p><strong>Bad:</strong></p>\n<blockquote>\n<p>The ”black mirror“ of the title is the one you‘ll find on every wall，on\nevery desk，in the palm of every hand：the cold，shiny screen of a TV，a\nmonitor，a smartphone。</p>\n</blockquote>\n<h3>使用完整名词，避免行业语言</h3>\n<p><strong>Good:</strong></p>\n<blockquote>\n<p>我们需要一位熟悉 JavaScript、HTML5，至少理解一种框架（如 Vue、AngularJS、React 等）的前端开发者。</p>\n</blockquote>\n<p><strong>Bad:</strong></p>\n<blockquote>\n<p>我们需要一位熟悉 js、h5，至少理解一种框架（如 vue、angular、react 等）的前端开发者。</p>\n</blockquote>\n<h2>参考文献</h2>\n<ul>\n<li><a href=\"https://sparanoid.com/note/chinese-copywriting-guidelines/\">中文文案排版指北</a></li>\n</ul>","frontmatter":{"date":"March 19, 2018","path":"/instructions-of-post","title":"推文说明","author":"Leon","dsct":"这将使我们的文档更美观更易于阅读","tags":["官方"]}}},"pathContext":{}}

